export function IconManager (icon:string){
    const mode = useSelector(selectLight);
    return (
        <svg className="icon" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill={ (mode) ? "black" : "white"}>
            {getImage(icon)}
        </svg>
    );
}

